<template>
  <right-page :header="rightPageHeader" :hide-content-padding="true">
    <div slot="content">
      <div class="slds-form-element section" :class="{'slds-has-error': showError}">
        <label class="slds-form-element__label capacity-label" for="chat-capacity">Chat Capacity (<span class="slds-form-element__help">Up to 100</span>)</label>
        <div class="slds-form-element__control">
          <input id="chat-capacity" class="slds-input capacity-input" type="number" min="1" v-model.number="chatCapacity.ChatsLimit" @keydown="checkNum($event, chatCapacity.ChatsLimit)"  @keyup="limitNum(chatCapacity.ChatsLimit, maxLimit)"/>
          <div class="label-info">The maximum number of chats an agent can engage in simultaneously.</div>
        </div>
      </div>
      <footer class="slds-modal__footer">
        <button class="slds-button slds-button_neutral" :disabled="!hasChanged || isSaving" @click="cancel">Cancel</button>
        <button class="slds-button slds-button_brand" :disabled="!hasChanged || isSaving || showError" @click="saveChatCapacity">Save</button>
      </footer>
    </div>
  </right-page>
</template>

<script>
import { getChatCapacity, saveChatCapacity } from '../api/charket-settings'
import CheckLimitNum from '../mixins/checkLimitNum.js'

export default {
  name: 'ChatCapacity',
  mixins: [CheckLimitNum],
  data () {
    return {
      rightPageHeader: {
        label: 'CHARKET SETTINGS',
        name: 'Chat Capacity'
      },
      chatCapacity: {
        ChatsLimit: null
      },
      isSaving: false,
      originalData: null,
      maxLimit: 100
    }
  },
  computed: {
    hasChanged () {
      return this.originalData !== this.chatCapacity.ChatsLimit
    }
  },
  created () {
    this.fetchChatCapacity()
  },
  methods: {
    async fetchChatCapacity () {
      try {
        this.$bus.emit('show-loading')
        const data = await getChatCapacity()
        this.originalData = data.ChatsLimit
        this.chatCapacity.ChatsLimit = data.ChatsLimit
      } catch (error) {
        this.$bus.emit('error', { content: error.msg, type: 'error' })
      } finally {
        this.$bus.emit('hide-loading')
      }
    },
    cancel () {
      this.chatCapacity.ChatsLimit = this.originalData
      this.showError = false
    },
    async saveChatCapacity () {
      try {
        this.isSaving = true
        await saveChatCapacity(JSON.stringify(this.chatCapacity))
        this.$bus.emit('success', { content: 'Chat Capacity was saved.', type: 'success' })
        this.originalData = this.chatCapacity.ChatsLimit
      } catch (error) {
        this.$bus.emit('error', { content: error.msg, type: 'error' })
      } finally {
        this.isSaving = false
      }
    }
  }
}
</script>

<style lang="scss" scoped>
  .section {
    padding: 20px 30px;
    .capacity-label, .label-info {
      color: #706E6B;
    }
    .slds-form-element__help {
      display: inline-block;
    }
    .capacity-input {
      width: 180px;
    }
  }
</style>
